@mixin center() {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.Showcase {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: rgba(99, 101, 100, 0.5);
    display: grid;
    grid-template-columns: 150px auto 150px;
    // padding: 100px;
    // .previous {
    //     // position: relative;
    // }
    .previous:hover {
        transition-duration: 0.5s;
        background: linear-gradient(to right, rgba(99, 101, 100, 0.5), rgba(99, 101, 100, 0)); 
    }
    .next:hover {
        transition-duration: 0.5s;
        background: linear-gradient(to left, rgba(99, 101, 100, 0.5), rgba(99, 101, 100, 0)); 
    }
    .container {
        // text-align: center;
        position: relative;
        // justify-self: center;
        // align-self: center;
        .closeIcon {
            position: absolute;
            top: 0; 
            left: 0;
            width: 50px;
            height: 50px;
            border-radius: 25px;
            background: black;
            color: white;
            font-size: 20px;
            font-weight: 700;
            span {
                @include center();
                display: inline-block;
            }
            opacity: 0;
            transition-duration: 0.5s;
        }
        .closeIconShow {
            opacity: 1 !important;

        }
    }
    img {
        @include center();
        display: block;
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
    }
    p {
        @include center();
        text-align: center;
        // display: inline-block;
        margin: 0px;
        font-size: 60px;
    }
}
