$background: rgb(255, 255, 255);
.Bond {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: $background;
    font-size: 11px;
    margin: 50px;
    .left {
        position: absolute;
        left: 0px; // right: 0px;
        top: 0px;
        bottom: 0px;
        width: 50%;
        text-align: right; // margin: 50px 0;
        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
    .right {
        position: absolute; // left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        width: 50%;
        text-align: left; // margin: 50px 0;
        .container {
            margin: 60px;
            padding: 15px;
            width: 50%;
            height: calc(100% - 150px);
            background: rgb(99, 101, 100);
        }
        .thumbnailContainer {
            width: auto;
            height: auto;
            max-height: 100%;
            max-width: 100%;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            display: grid;
            grid-template-columns: auto auto; // grid-template-columns: repeat(auto);
            grid-column-gap: 10px;
            grid-row-gap: 10px;
        }
    }
}

.Thumbnail {
    img {
        display: block;
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
    }
}