$background: rgb(255, 255, 255);;

.Design {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: $background;
    font-size: 11px;
    .container {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        border: 3px solid rgb(255, 255, 255);
        text-align: center;
        margin: 50px;
    }
    h1 {
        font-size: 35px;
        font-family: "Goudy Stout";
        font-weight: 400;
        margin-top: 0px;
        margin-bottom: 15px;
    }
    .iconContainer {
        margin-top: 70px; // margin-bottom: 50px;
    }
    .iconBottom {
        position: absolute;
        height: 92.5px;
        width: 94px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);    
    }
}

.IconWTitle {
    display: inline-block;
    width: 281px;
    margin: 0 0;
    padding: 5px;
    border-radius: 4px;
    background: $background;
    img {
        width: 281px;
        height: 170px;
    }
    p {
        font-size: 25px;
        font-family: "Goudy Stout";
        // font-weight: 700;
        margin-top: 0px;
        margin-bottom: 0px;
    }
}

.IconWTitle:active {
    transition-duration:0.05s !important;
    box-shadow: 0 0 !important;
    background: darken($background, 10%) 
}

@mixin iconWTitle-hover($direction) {
    transition-duration:0.5s;
    box-shadow: unquote( $direction + " 10px 5px " + #888888 );
}

.IconWTitle1:hover {
    @include iconWTitle-hover('-5px -5px');
}
.IconWTitle2:hover {
    @include iconWTitle-hover('5px -5px');
}
.IconWTitle3:hover {
    @include iconWTitle-hover('-5px 5px');
}
.IconWTitle4:hover {
    @include iconWTitle-hover('5px 5px');
}
